* {
  font-size: 1rem;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
body {
  padding: 0;
  margin: 0;
  background-color: #f1f1f1;
}

.logo {
  padding: 1rem 0;
  font-weight: bolder;
  font-family: Arial, Helvetica, sans-serif;
}

.logo a {
  border-top: 3px solid #ff4a54;
  border-bottom: 3px solid transparent;
  font-size: 1.2rem;
  text-decoration: none;
  color: black;
  transition-duration: 0.5s;
  padding-bottom: 5px;
}

.logo a:hover {
  border-bottom: 3px solid #ff4a54;
}
/*# sourceMappingURL=Logo.css.map */
nav {
  background-color: white;
  z-index: 999999;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  display: flex;
  border-bottom: 1px solid #ddd;
  padding: 0 1rem;
}

nav .menu {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
}

nav .menu li {
  margin: 0 1rem;
  list-style: none;
}

nav .menu li:hover .sub-menu {
  z-index: 1;
  opacity: 1;
}

nav .menu li a.active {
  font-weight: bold;
  border-bottom: 3px solid #ff4a54;
}

nav .menu li .item {
  outline: none;
  cursor: pointer;
  border: none;
  color: black;
  text-decoration: none;
  padding: 1rem 0.25rem;
  border-bottom: 3px solid transparent;
  transition-duration: 0.5s;
}

nav .menu li .item:hover {
  border-bottom: 3px solid #ff4a54;
}

nav .menu li ul.sub-menu {
  padding: 0px;
  padding-top: 1rem;
  position: absolute;
  transition: opacity linear 0.15s;
  z-index: -1;
  opacity: 0;
  background-color: white;
  border-bottom: 3px solid #ff4a54;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

nav .search-btn {
  cursor: pointer;
  padding: 1.2rem;
  outline: none;
  border: none;
  background-color: #ff4a54;
  transition-duration: 0.5s;
  color: white;
  font-weight: bolder;
}

nav .search-btn:hover {
  background-color: gray;
}

nav .navBtn {
  display: none;
}

nav .navBtn button {
  cursor: pointer;
  padding: 1.2rem;
  outline: none;
  border: none;
  background-color: transparent;
  transition-duration: 0.5s;
  font-weight: bolder;
  font-size: 1.2rem;
  color: #ff4a54;
}

nav .navBtn button:hover {
  color: gray;
}

@media (max-width: 750px) {
  nav .logo {
    flex: 1 1;
  }
  nav .menu {
    display: none;
  }
  nav .search-btn {
    display: none;
  }
  nav .navBtn {
    display: block;
    font-size: 2rem;
  }
}
/*# sourceMappingURL=NavBar.css.map */
.add-new-post {
  width: 100vw;
  height: 90vh;
}

.add-new-post .form-wrapper {
  width: 60%;
  margin: 1rem;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border: 1px solid #ddd;
  padding: 1rem;
  border-radius: 8px;
}

.add-new-post .form-wrapper form {
  display: flex;
  flex-direction: column;
}

.add-new-post .form-wrapper form input,
.add-new-post .form-wrapper form label,
.add-new-post .form-wrapper form textarea {
  flex: 1 1;
  border-radius: 5px;
  border: none;
}

.add-new-post .form-wrapper form label {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 0.5rem 0;
}

.add-new-post .form-wrapper form input,
.add-new-post .form-wrapper form textarea {
  padding: 0.5rem 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transition-duration: 0.5s;
  outline-color: gray;
}

.add-new-post .form-wrapper form input:focus,
.add-new-post .form-wrapper form textarea:focus {
  outline-color: #ff4a54;
}

.add-new-post .form-wrapper form textarea {
  max-width: 100%;
  min-width: 100%;
}

.add-new-post .form-wrapper form .publish-btns {
  padding: 1rem 0;
  display: flex;
  justify-content: space-between;
}

.add-new-post .form-wrapper form .publish-btns button,
.add-new-post .form-wrapper form .publish-btns div,
.add-new-post .form-wrapper form .publish-btns a {
  border: none;
  cursor: pointer;
  padding: 0.5rem 2rem;
  display: block;
  border-radius: 8px;
  transition-duration: 0.5s;
  font-weight: bolder;
  background-color: #ddd;
  text-decoration: none;
  color: black;
}

.add-new-post .form-wrapper form .publish-btns button:hover,
.add-new-post .form-wrapper form .publish-btns div:hover,
.add-new-post .form-wrapper form .publish-btns a:hover {
  background-color: gray;
  color: white;
}

.add-new-post .form-wrapper form .publish-btns :first-child {
  color: white;
  background-color: #ff4a54;
}

.add-new-post .form-wrapper form .publish-btns div {
  background-color: #ff4a54;
  padding-right: 2.7rem;
  padding-left: 2.7rem;
}

@media (max-width: 750px) {
  .add-new-post {
    display: block;
    width: auto;
    height: auto;
  }
  .add-new-post .form-wrapper {
    width: auto;
  }
}
