.posts {
  margin: 1rem;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
  flex-wrap: wrap;
}

.posts .post {
  background-color: white;
  transition: 0.5s;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border: 1px solid #ddd;
  padding: 1rem 0.5rem;
  padding-bottom: 2rem;
  border-radius: 8px;
}

.posts .post:hover {
  transform: scale(1.1);
}

.posts .post a {
  text-decoration: none;
  padding: 0.5rem 1.5rem;
  border: none;
  outline: none;
  background-color: #ff4a54;
  border-radius: 8px;
  cursor: pointer;
  color: white;
  transition: background-color ease 0.5s 0s;
}

.posts .post a:hover {
  background-color: gray;
}

@media (max-width: 750px) {
  .posts {
    display: block;
  }
  .posts .post {
    margin: 1rem 0;
  }
}
/*# sourceMappingURL=Post.css.map */
.single-post {
  display: flex;
  justify-content: space-between;
}

.single-post main {
  margin: 1rem;
  flex-basis: 70%;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: white;
}

.single-post main .post-thumbnail {
  background-color: #343a40 !important;
  padding: 2rem 1rem;
  color: white;
}

.single-post main .post-thumbnail h1 {
  margin-top: 1rem;
  font-style: italic;
  font-size: 3rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.single-post main .post-thumbnail P {
  color: #ddd;
}

.single-post main .post-content {
  margin-top: 1rem;
}

.single-post aside {
  flex-basis: 30%;
}

@media (max-width: 750px) {
  .single-post {
    display: block;
  }
  .single-post aside {
    display: none;
  }
}
