  color: #ff4a54;
  transition: color ease 0.5s 0s;
}

.header ul li a:hover {
  color: black;
}

.post-list {
  border-radius: 5px;
  margin: 1rem;
}

.post-list .table-row {
  margin: 0.5rem 0;
