.shadow {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.sidebar {
  background-color: #333;
  position: fixed;
  width: 20vw;
  right: 0;
  height: 100vh;
  padding: 1rem;
}

.sidebar .content {
  text-align: right;
}

.sidebar .content .active {
  background-color: #ddd;
  color: #333;
}

.sidebar .content button {
  cursor: pointer;
  display: inline-block;
  transform: rotate(45deg);
  background: transparent;
  outline: none;
  border: none;
}

.sidebar .content button li {
  color: white;
  font-size: 2rem;
}

.sidebar .content ul {
  margin: 0;
  padding: 0;
  margin-top: 2rem;
}

.sidebar .content ul li {
  list-style: none;
}

.sidebar .content ul li a {
  display: block;
  padding: 1rem 0;
  padding-right: 0.5rem;
  border-bottom: 1px solid #ddd;
  text-decoration: none;
  color: white;
  font-weight: 800;
  transition-duration: 0.5s;
}

.sidebar .content ul li a:hover {
  background-color: #ddd;
  color: #333;
}

@media (max-width: 750px) {
  .sidebar {
    width: 50vw;
  }
}

@media (min-width: 750px) {
  .sidebar,
  .shadow {
    display: none;
  }
}
